<template>

    <div class="questions-test modal">
      <div class="test-timer">
        <TestTimer></TestTimer>
      </div>

      <div class="questions-test-wrapper">

        <TestHeader
        :textTitle=titleHeader
      />

      <TestBody
        v-if="actualView"
        :bodyData=actualView.body
      />
      <div
        v-else
      >
        <br><br>
        Не удалось загрузить данные с сервера
        <br><br>
        <button @click="$router.push(`/main`)">На главную</button>
        <br><br>
      </div>

      <TestFooter class="questions-test-btn active"
                  v-if="questionButtonsList"
                  :buttonsList=questionButtonsList
      />

      </div>


    </div>

</template>

<script>
  // import localTestData from '@/data/data2.json'
  import {mapActions, mapGetters} from 'vuex'
  import TestHeader from '@/components/TestHeader.vue'
  import TestBody from '@/components/TestBody.vue'
  import TestFooter from '@/components/TestFooter.vue'
  import TestTimer from '@/components/TestTimer.vue'
  

  export default {
    components: {
      TestHeader,
      TestBody,
      TestFooter,
      TestTimer
    },
    data() {
      return {
        questionButtonsList: [
          {
            "name": "Далее",
            "action": "next"
          }
        ]
      }
    },
    computed: {
      ...mapGetters([
        'actualView',
        'stepNumber',
        'totalQuestions',
        'sectionList'
      ]),
      titleHeader() {
        return `Вопрос ${this.stepNumber + 1} из ${this.totalQuestions}`
      }
    },
    methods: {
      ...mapActions([
        // 'GET_DATA_FROM_LOCAL',
        'GET_TEST'
      ])
    },
    created() {
      // this.GET_DATA_FROM_LOCAL(localTestData)
      // console.log(this.$route.params.testNumber);
      // console.log(this.sectionList[this.$route.params.testNumber].serverTestId);
      this.GET_TEST(this.sectionList[this.$route.params.testNumber].serverTestId)
    }

  }
</script>

