<template>
    <div class="main-wrapper">
      1111111    
        <div v-if="isAuth">
          <div class="header-container">
            <MainHeader/>
            <LoginHeader/>
          </div>

            <p class="main-info">
                Работа с платформой ситуационного обучения проходит последовательно в три этапа:
            </p>
            <div
              v-for="section,index in sectionList"
              :key="section.serverTestId"
              class="sections"
            >
                <HomePageSection
                    :sectionData = section
                    :sectionIndex = index
                />
            </div>
            <p class="main-info">
                Внутри каждого раздела можно посмотреть короткое ознакомительное видео и прочитать инструкции.
            </p>
            <!-- <p class="main-info">
                Платформа доступна на компьютерах, смартфонах и планшетах.
            </p> -->
        </div>
    </div>
</template>

<script>
  import {mapActions, mapGetters} from 'vuex'
  import LoginHeader from '@/components/LoginHeader.vue'
  import HomePageSection from '@/components/HomePageSection.vue'
  import MainHeader from '@/components/MainHeader.vue'

export default {
  components: {
    LoginHeader,
    HomePageSection,
    MainHeader
  },
  computed: {
    ...mapGetters([
      'isAuth',
      'testsSummary',
      'sectionList'
    ])
  },
  methods: {
    ...mapActions([
        'GET_TEST_SUMMARY'
    ]),

    rightAnswers(id){
      return this.testsSummary.find(thing => thing.id === id).rightAnswers
    }
  },
  mounted() {
    this.GET_TEST_SUMMARY()
  },
}
</script>

