<template>
  <div class="body-training">
    <!-- <BodyTrainingSection
      v-for="trainingSection in trainingData"
      :key="trainingSection.body.title"
      :trainingSection="trainingSection.body"
    /> -->

    <!-- Загрузка с сервера -->
    <!-- <BodyTrainingSection
      v-for="trainingSection in trainingList"
      :key="trainingSection.body.title"
      :trainingSection="trainingSection.body"
    /> -->

    <!-- from state -->
    <!-- <BodyTrainingSection
      v-for="trainingSection,index in trainingData"
      :key="trainingSection.body.title"
      :trainingSection="trainingSection.body"
      :trainingIndex="index"
    /> -->

    <BodyTrainingSection
      v-for="trainingSection,index in trainingQuestions"
      :key="trainingSection.body.title"
      :trainingSection="trainingSection.body"
      :trainingIndex="index"
      :completed = "completedList[index]"
      @click="clickTraining(index)"
    />

  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
import BodyTrainingSection from '@/components/BodyTrainingSection.vue'

export default {
  data() {
    return {
      completedList: [],
      userTrainingKey: ""
    }
  },
  components:{
    BodyTrainingSection
  },
  props:{
    trainingQuestions: []
  },
  computed: {
    ...mapGetters([
      'trainingList',
      'login',
      'actualSection'
    ]),

    summaryCompleted(){
        let summary = 0
        for (let i = 0; i < this.completedList.length; i++) {
          if (this.completedList[i]) {
            summary += 1
          }
        }
        return summary
      }
  },
  methods: {
    ...mapActions([
        'setTrainingRightAnswers'
    ]),

    clickTraining(index){
      this.completedList[index] = true
      this.saveCompleted(this.completedList)
      
      if (this.summaryCompleted === this.completedList.length) {
        this.setTrainingRightAnswers(this.completedList.length)
      }
      console.log(this.trainingQuestions[index]);
      window.open(this.trainingQuestions[index].body.link, '_blank')
    },

    saveCompleted(completedList){
      const value = JSON.stringify(completedList)
      localStorage.setItem(this.userTrainingKey, value)
    },

    loadCompletedFromStorage(){
      const completedFromLocal =  localStorage.getItem(this.userTrainingKey)
      if (completedFromLocal) {        
        const arrayTrainingLocal = JSON.parse(completedFromLocal)
        if  (arrayTrainingLocal.length === this.trainingQuestions.length) {
          this.completedList = arrayTrainingLocal
        }
        else {
          for (let i = 0; i < this.trainingQuestions.length; i++) {
            this.completedList[i] = false      
          }
          this.saveCompleted(this.completedList)
        }
      }
      else{
        for (let i = 0; i < this.trainingQuestions.length; i++) {
            this.completedList[i] = false      
          }
          this.saveCompleted(this.completedList)
      }
    }

  },
  mounted() {
    // localStorage.clear()
    this.userTrainingKey = 'training' + this.actualSection.serverTestId + this.login    
    this.loadCompletedFromStorage()
  },
}
</script>

