<template>
  <div>
    {{ currentMinutes }} : {{ currentSeconds }}
  </div>
</template>

<script>
import {mapActions} from 'vuex'

export default {
  data() {
    return {
      currentMinutes: 30,
      currentSeconds: 0,
      timerId: ""
    }
  },    
  methods: {
    ...mapActions([
        'endByTimer'
    ]),

    countdownTimer(){
      if (this.currentMinutes === 0 && this.currentSeconds === 0) {
        clearInterval(this.timerId)
        this.endByTimer()
      }
      else{        
        if (this.currentSeconds===0) {
          this.currentSeconds = 59
          this.currentMinutes -= 1
        }
        this.currentSeconds -= 1
      }
    },
    
    startTimer(){
      this.timerId = setInterval(this.countdownTimer, 1000);
    }
  },
  mounted(){
    this.startTimer()
  }

  
}
</script>

