<template>
  <!--Старт теста {{ $route.params.testNumber }}-->
  <div class="test-wrapper start-test">

    <TestHeader
      :textTitle="testTitle"
    />

    <TestBody
      :bodyData=bodyData
    />

    <TestFooter
      :buttonsList=buttonsList
    />

  </div>
</template>

<script>
import {mapGetters} from 'vuex'
import TestHeader from '@/components/TestHeader.vue'
import TestBody from '@/components/TestBody.vue'
import TestFooter from '@/components/TestFooter.vue'

export default {
  data() {
    return {
      bodyData: {
        "text": "Ответьте на 30 тестовых вопросов." + "<br><br>" +
          "У каждого вопроса будет 4 варианта ответа, нужно выбрать верный и нажать кнопку “Ответить”" + "<br>" +
          "После этого будет показан следующий вопрос." + "<br><br>" +
          "После завершения тестирования будет показан общий результат." + "<br><br><br> " +
          "Ознакомительное видео покажет, как все будет происходить:",
        "videoPath": ""
      },
      buttonsList: [
        {
          "name": "Начать тестирование",
          "action": "startTest"
        }
      ]
    }
  },
  components: {
    TestHeader,
    TestBody,
    TestFooter
  },
  computed: {
    ...mapGetters([
      'isAuth',
      'sectionList',
      'actualSection',
      'currentSectionNumber'
    ]),
    testTitle(){
      return `Этап ${this.currentSectionNumber+1}. ${this.actualSection.name}`
    }    
  },
  mounted() {
    this.bodyData.videoPath = this.actualSection.videoPath
  },
}
</script>

