<template>
  <div class="user">
    <div class="img-container">
      <img src="@/assets/images/default_user_avatar.png">
    </div>
    <div class="user-data">
      <div class="user-name">
        <span>Пользователь:</span>
        <span>{{ login }}</span>
      </div>
      <div @click="userExit" class="btn outline-btn">Выйти</div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions} from 'vuex'

export default {
  computed: {
    ...mapGetters([
      'userName',
      'login'
    ]),    
  },
  methods: {
    ...mapActions([
        'SIGN_OUT',
    ]),
    userExit(){
      this.SIGN_OUT()
      this.$router.push('/')
    }
  }
}
</script>

