<template>
  <div class="body-answer">
    <p class="answer-header">Выберите верный ответ:</p>

    <div class="answers-container"
      v-if="answers.type==='text'"
    >
      <div
        v-for="answer,index in answers.items"
        :key="answer.text"
      >
        <input type="radio" :id="answer+index" :value="index" v-model="picked" />
        <label :for="answer+index">{{answer.text}}</label>

      </div>
    </div>

    <div
      v-if="answers.type==='image'"
      class="answer-image-list"
    >
      <div
        v-for="answer,index in answers.items"
        :key="answer.text"
      >
        <div class="answer-image-item">

          <!--<label :for="answer+index">-->
          <!--<div class="img-container">-->
          <!--<img :src="questionImage(answer.image)" alt="image" class="body-image">-->
          <!--</div>-->
          <!--</label>-->
          <input type="radio" :id="answer+index" :value="index" v-model="picked" />
          <label :for="answer+index">
            <div class="img-container">
              <img :src="questionImage(answer.image)" alt="image" class="body-image">
            </div>
          </label>

          <!--<input type="radio" :id="answer+index" :value="index" v-model="picked"/>-->
          <!--<label :for="answer+index">{{answer.text}}</label>-->

        </div>


      </div>

    </div>

    <!-- <br />
      <span>Выбрано: {{ picked }}</span>

    <br><br><br> -->

  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  data() {
    return {
      picked: ""
    }
  },
  props:{
    answers: {}
  },
  computed:{
    ...mapGetters([
      'currentAnswerIndex'
    ])
  },
  methods: {
    ...mapActions([
        'setCurrentAnswerIndex'
    ]),
    questionImage(answerImage){
      return require(`@/assets/images/${answerImage}`)
    }
  },
  watch:{
    picked(newPick){
      this.setCurrentAnswerIndex(newPick)
    },
    currentAnswerIndex(newAnswerIndex){
      if (newAnswerIndex === "") {
        this.picked = ""
      }
    }
  },


}
</script>

