// import { createRouter, createWebHistory } from 'vue-router'
import { createRouter, createWebHashHistory } from 'vue-router'


import LoginPage from '@/components/LoginPage.vue'
import MainPage from '@/components/MainPage.vue'
import StartTest from '@/components/StartTest.vue'
import QuestionsTest from '@/components/QuestionsTest.vue'
import FeedbackTest from '@/components/FeedbackTest.vue'
import EndTest from '@/components/EndTest.vue'
import StartTraining from '@/components/StartTraining.vue'
import TrainingTests from '@/components/TrainingTests.vue'

const routes = [
  {
    path: '/',
    name: 'login',
    component: LoginPage
  },
  {
    path: '/main',
    name: 'main',
    component: MainPage
  },
  {
    path: '/test:testNumber/start',
    component: StartTest
  },
  {
    path: '/starttraining',
    component: StartTraining
  },
  {
    path: '/test:testNumber/questions',
    component: QuestionsTest
  },
  {
    path: '/test:testNumber/feedback',
    component: FeedbackTest
  },
  {
    path: '/test:testNumber/endtest',
    component: EndTest
  },
  {
    path: '/training',
    component: TrainingTests
  }
  
]

const router = createRouter({
  // history: createWebHistory(process.env.BASE_URL),
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
