<template>
  <div>    
      <div
      class="sections-row"
      :class="{
        active: sectionData.status==='ready',
        complited: sectionData.status==='completed'
      }"
    >
      <div class="sections-row__number">{{sectionNumber}}</div>
      <div class="sections-row__description">
        <div

        class="heading">{{sectionData.name}}</div>
        <div class="desc">{{sectionData.description}}</div>
      </div>
        <div
          v-if="sectionData.status==='ready'"
          @click="sectionClick"
          class="btn rounded-btn">Начать
        </div>
        <div
          v-if="sectionData.status==='completed'"
          v-html=textCompleted
          class="counter-right-answers"
        >
        
        </div>
        <div
          v-if="sectionData.status==='notActive'"
          class="btn rounded-btn disable">Будет доступно после прохождения предыдущего этапа.
        </div>

    </div>

  </div>
</template>

<script>
import {mapActions} from 'vuex'

export default {
  props:{
    sectionData: {},
    sectionIndex: Number
  },
  computed:{
    sectionNumber(){
      return this.sectionIndex+1
    },
    textCompleted(){
      let textRightAnswers = `Завершено:<br>${this.sectionData.showAnswerText? "Правильных ответов" : ""} ${this.sectionData.rightAnswers} из ${this.sectionData.rightAnswers + this.sectionData.wrongAnswers}`
      return textRightAnswers
    }
  },
  methods: {
    ...mapActions([
      'setCurrentSectionNumber'
    ]),

    sectionClick(){
      this.setCurrentSectionNumber(this.sectionIndex)
      this.$router.push(this.sectionData.routeStart)
    }
  },
  
}
</script>

