<template>
  <div class="questions-img">
    <div class="img-container">
      <img :src="questionImage" alt="button image" class="body-image">
    </div>
  </div>
</template>

<script>

export default {
  props:{
    bodyImage: String   
  },
  computed: {
    questionImage() {
        return require(`@/assets/images/${this.bodyImage}`)
    },
  }
}
</script>

<style lang="scss">
  .body-image{
    max-width: 400px;
  }
</style>