<template>
  <div class="body-feedback">
    <div class="feedback-item">
      <p class="description-item">{{ feedbackQuestion.body.intuitiveInterface }}</p>
      <div class="stars-item">

        <div class="fb-stars">
          <div
            v-for="num, ind in stars"
            :key="`st-${ind}`"
            class="cursor-pointer"
            @mouseover="starHover = num"
            @mouseleave="starHover = 0"
            @click="setFeedbackAnswer('intuitiveInterface', num)"
          >
            <img
              v-if="feedbackAnswer.intuitiveInterface < num"
              src="@/assets/images/star.svg"
              alt="star"
              class="fb-star"
            />
            <img
              v-if="feedbackAnswer.intuitiveInterface >= num"
              src="@/assets/images/star_full.svg"
              alt="star"
              class="fb-star"
            />
          </div>
        </div>

      </div>
    </div>

    <div class="feedback-item">
      <p class="description-item">{{ feedbackQuestion.body.actuality }}</p>
      <div class="stars-item">

        <div class="fb-stars">
          <div
            v-for="num, ind in stars"
            :key="`st-${ind}`"
            class="cursor-pointer"
            @mouseover="starHover = num"
            @mouseleave="starHover = 0"
            @click="setFeedbackAnswer('actuality', num)"
          >
            <img
              v-if="feedbackAnswer.actuality < num"
              src="@/assets/images/star.svg"
              alt="star"
              class="fb-star"
            />
            <img
              v-if="feedbackAnswer.actuality >= num"
              src="@/assets/images/star_full.svg"
              alt="star"
              class="fb-star"
            />
          </div>
        </div>

      </div>
    </div>

    <div class="feedback-item">
      <!-- <p class="description-item">Пожалуйста, оставьте ваш комментарий по данному курсу:</p> -->
      <p class="description-item">{{ feedbackQuestion.body.userAttention }}</p>
      <!--<div class="area-item"></div>-->
      <textarea
        @change="setFeedbackAnswer('userAttention', $event.target.value)"
        class="area-item"
      >
      </textarea>
    </div>

    <div class="feedback-item">
      <p class="description-item">{{ feedbackQuestion.body.neededThemes }}</p>
      <!--<div class="area-item"></div>-->
      <textarea
      @change="setFeedbackAnswer('neededThemes', $event.target.value)"
        class="area-item"
        >
      </textarea>
    </div>

  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'

export default {
  data() {
    return {
      stars: [1, 2, 3, 4, 5],
    }
  },
  computed:{
    ...mapGetters([
      'feedbackQuestion',
      'feedbackAnswer'
    ])    
  },
  methods: {
    ...mapActions([
      'setFeedbackItem'
    ]),

    setFeedbackAnswer(name,value) {
      this.setFeedbackItem({"name": name, "value": value})   
    }    
  },
  mounted() {
    console.log(this.feedbackQuestion);
  },
}
</script>

