<template>
  <!--Старт теста {{ $route.params.testNumber }}-->
  <div class="test-wrapper start-training">

    <TestHeader
      :textTitle="testTitle"
    />

    <TestBody
      :bodyData=bodyData
    />

    <TestFooter
      :buttonsList=buttonsList
      class="test-btn"
    />

  </div>
</template>

<script>
import {mapGetters} from 'vuex'
import TestHeader from '@/components/TestHeader.vue'
import TestBody from '@/components/TestBody.vue'
import TestFooter from '@/components/TestFooter.vue'

export default {
  data() {
    return {
      bodyData: {
        "text": "Пройдите ситуационные задачи. " + "<br><br>" +
                "Прохождения задач не оценивается. Для завершения этапа вы должны пройти все задачи. " + "<br>" +
                "Вы сможете возвращаться многократно к этим ситуационным задачам." + "<br><br><br><br><br><br>" +
                "Ознакомительное видео:",
        "videoPath": "",
      },
      buttonsList: [
        {
          "name": "Начать обучение",
          "action": "startTraining"
        }
      ]
    }
  },
  components: {
    TestHeader,
    TestBody,
    TestFooter
  },
  computed: {
    ...mapGetters([
      'isAuth',
      'sectionList',
      'actualSection',
      'currentSectionNumber'
    ]),
    testTitle(){
      return `Этап ${this.currentSectionNumber+1}. ${this.actualSection.name}`
    }
  },
  mounted() {
    this.bodyData.videoPath = this.actualSection.videoPath
  },
}
</script>

