<template>
  <!--<div class="active">-->

    <AnswerModalWarning
      v-if="showAnswerModal"
      :warningText = modalText
      :isRight="isRightModal"
    />
<!--для изменеия состояния кнопки дописать\убрать класс модификатор ''disable''-->
    <div 
        @click="buttonAction"
        :class="{disable: isTest && currentAnswerIndex===''}"
        class="btn outline-btn"
    >
      {{buttonProperties.name}}
    </div>
  <!--</div>-->

</template>

<script>
  import {mapGetters, mapActions} from 'vuex'
  import AnswerModalWarning from '@/components/AnswerModalWarning.vue'

export default {
  data() {
    return {
      showAnswerModal: false,
      modalText: "",
      isRightModal: false
    }
  },
  components:{
    AnswerModalWarning
  },
  props: {
    buttonProperties:{}
  },
  computed:{
    ...mapGetters([
      'actualView',
      'stepNumber',
      'questionsList',
      'currentAnswerIndex',
      'sectionList',
      'currentSectionNumber',
      'feedbackQuestion',
      'feedbackAnswer',
      'isTest'
    ])
    
  },
  methods: {
    ...mapActions([
        'setNextStep',
        'setCurrentAnswerIndex',
        'SEND_ANSWER',
        'SEND_FEEDBACK',
        'changeIsTest'
    ]),

    sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    },

    async buttonAction(){
      if (this.buttonProperties.action === "startTest") {
        this.changeIsTest(true)
        this.$router.push(`/test${this.currentSectionNumber}/questions`)
      }
      if (this.buttonProperties.action === "startTraining") {
        this.$router.push(`/training`)
      }
      
      if (this.buttonProperties.action === "next") {
        if (this.currentAnswerIndex === "") {
          
          // this.modalText = "Выберите ответ"
          // this.isRightModal = false
          // this.showAnswerModal = true
          // await this.sleep(500)
          // this.showAnswerModal=false

        } 
        else {
          // const isRightAnswer = this.actualView.body.answers.items[this.currentAnswerIndex].isRight
          // if (isRightAnswer) {
          //   this.modalText = "Ответ верный!"
          // }
          // else{
          //   this.modalText = "Ответ неверный!"
          // }
          // this.isRightModal = isRightAnswer
          // this.showAnswerModal = true
          // await this.sleep(600)
          // this.showAnswerModal=false

          const dataAnswer = {
            "question_id": this.questionsList[this.stepNumber].id,
            "test_id": this.questionsList[this.stepNumber].test_id,
            "body": {
              "description": {"answerIndex": this.currentAnswerIndex}
            }              
          }
          this.SEND_ANSWER(dataAnswer)
        }
      }
      
      if (this.buttonProperties.action === "toMain") {
        this.$router.push(`/main`)
      }

      if (this.buttonProperties.action === "toEndTest") {
        const dataFeedbackAnswer = {
            "question_id": this.feedbackQuestion.id,
            "test_id": this.feedbackQuestion.test_id,
            "body": {
              "description":this.feedbackAnswer
            }              
          }
                
        this.SEND_FEEDBACK(dataFeedbackAnswer)
        // this.$router.push(`/test${this.currentSectionNumber}/endtest`)
      }
    }
  }
}
</script>

